
import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { useState } from "react";
import SearchBar from "./searchbar";
import ShoppingCart from "./cartitems";
import { useNavigate } from "react-router-dom";
import { shopContext } from "../context/shopContext";
import Productlist from "./productlist";



function Navbar({ onSearch}) {
  const [searchInput, setSearchInput] = useState("");
  const[maxPrice,setMaxprice]= useState("");
  const[minPrice,setMinprice]= useState("");

 const{findproduct,cartItems,filterProductsByPriceRange} = useContext(shopContext);

 const totalItemsInCart = Object.values(cartItems).reduce((acc, item) => acc + item, 0);
 const navigate = useNavigate();

  const handleclick = (e) => {
    e.preventDefault();
    navigate("/cart")
  }
  const handleInputChange = (e) => {
    setSearchInput(e.target.value);
  };
  const handlesubmit=  async(e)=>{
    e.preventDefault();
    const foundProduct = await findproduct(searchInput);
    setSearchInput("");
    console.log("foundProduct",foundProduct)
    onSearch(foundProduct);
    navigate("/list");


  }
  const handleMinPriceChange = (e) => {
    setMinprice(e.target.value);
  };

  const handleMaxPriceChange = (e) => {
    setMaxprice(e.target.value);
  };
  const handlePrice =(e)=>{
    e.preventDefault();
    const filteredProducts = filterProductsByPriceRange(Number(minPrice), Number(maxPrice));
    console.log('filteredProducts', filteredProducts);
    onSearch(filteredProducts)
  }

  return <>
    <div className="container">
      <nav className="navbar navbar-expand-lg ">
        <img src="/images\Logo.png" className="navbar-brand" href="#"></img>
        <ul className=" navbar-nav mynav">
          <li className="nav-item">
            <Link to="/home" id="home">Home</Link>
          </li>
          <li className="nav-item">
          <Link to="/list" id="shop">Shop</Link>
          </li>
          <div className="search">
            <form onSubmit={handlesubmit}>
              <span className='wraper'>
                <input placeholder='Search for product...' type="text" value={searchInput} onChange={ handleInputChange}></input>
                <button type="submit"><img src='/images/search.png'></img></button>
              </span>
            </form>
            <div className="price-range">
        <input
          type="number"
          placeholder="Min"
          value={minPrice}
          onChange={handleMinPriceChange}
         
        />
        <input
          type="number"
          placeholder="Max"
          value={maxPrice}
          onChange={handleMaxPriceChange}
       
        
        />
        <button type="submit"  onClick={handlePrice} >
          Filter
        </button>
      </div>
            <div>
              <div className="account"><img src='/images/user.png'></img></div>
              <div className="account-cart" onClick={handleclick}> <img src='/images/shopping-basket.png'></img>
              <span>{totalItemsInCart}</span>
              </div>
            </div>
          </div>
  
        </ul>

      </nav>
    </div>

  </>
}
export default Navbar;