import React, { useContext, useState } from "react";
import { ProductData } from "../context/initials";
import { shopContext } from "../context/shopContext";
import ShoppingCart from "./cartitems";
import { useNavigate } from "react-router-dom";



export const ShopCart =()=>{
    const data = ProductData;

    const navigate = useNavigate()
    const {cartItems,getTotalCartamount} = useContext(shopContext);
    const totalAmount = getTotalCartamount();
    const handleClick=(e)=>{
        e.preventDefault();
        navigate("/address")


    }
    
    console.log("hellooo",cartItems)
return(
    <>
    {data.map((product)=>{
        if(cartItems[product.id] && cartItems[product.id] !== 0){
            return<ShoppingCart key={product.id} product ={product}></ShoppingCart>
        }
    })}
    <div className="container">
        <div className="row">
            <div className="col-lg-12 offset-lg-7">
    {totalAmount > 0?(
     <div className="total">
    <div className="product-title">GrandTotal : ${totalAmount} </div>
    <div>
    <button onClick={handleClick}  className="next">Next</button>
    </div>

    </div>)
:<h1>Your cart is empty</h1>}
</div>
</div>
</div>
    </>
)

}