import React, { useContext,useState } from "react";
import TextField from "./textField";
import { shopContext } from "../context/shopContext";
import { useFormik } from 'formik';
import * as Yup from 'yup';
import Popup from "./popup";
import { useNavigate } from "react-router-dom";


function AddressForm(){
  
  const{ addShippingDetails} = useContext(shopContext);
  const{loading, setLoading} = useState(false);
  const [orderPlaced, setOrderPlaced] = useState(false);
  const navigate = useNavigate();

  const formik = useFormik({
    initialValues: {
      firstname: '',
      lastname: '',
      address:"",
      country:"",
      code:"",
      state:""

    },
    //validation
    validationSchema: Yup.object({

      firstname: Yup.string().required('Required'),
      lastname: Yup.string().required('Required'),    
      address: Yup.string().required('Required'),
      country: Yup.string().required('Required'),   
      code: Yup.string().required('Required'),
      state: Yup.string().required('Required'),   
    }),
    onSubmit: values => {
      console.log("handlingsubmit")
      addShippingDetails({
        firstname: values.firstname,
        lastname: values.lastname,
        address: values.address,
        country: values.country,
        code: values.code,
        state: values.state,
      })
      setOrderPlaced(true);
      setTimeout(() => {
        setOrderPlaced(false);
   navigate("/logout")

        
      }, 3500);
    },
  });

    return<>
     <div className="container">
        <div className="row">
            <div className="col-lg-6 col-md-6">
            {/* <h1>Shipping</h1>
  <p>Please enter your shipping details.</p> */}
  <form onSubmit={formik.handleSubmit} >
    <span>
    <TextField type="text" name="firstname" display="FirstName" formik={formik} placeholder="FirstName"></TextField>
    <TextField type="text" name="lastname" display="LasttName" formik={formik} placeholder="LasttName"></TextField>
    </span>
    <TextField type="text" name="address" display="Address" formik={formik} placeholder="Address"></TextField>
    <TextField type="text" name="country" display="Country" formik={formik} placeholder="Country"></TextField>
    <TextField type="text" name="code" display="zipcode" formik={formik} placeholder="code"></TextField>
    <TextField type="text" name="state" display="state" formik={formik} placeholder="state"></TextField>
    <button class="button"  type="submit">Continue</button>
  </form>

            </div>
        </div>
        {orderPlaced && (
           <Popup status={orderPlaced}></Popup>
      )}
     </div>
    
    </>
}
export default AddressForm