import React, { createContext, useState, useEffect } from 'react';
import { ProductData } from './initials';


let productitem = ProductData;


export const shopContext = createContext();


const getDefaultCart = () => {

    let cart = {};
    for (let i = 1; i < productitem.length; i++) {
        cart[i] = 0
    }
    return cart;
};
export const ShopContextProvider = (props) => {

    const [cartItems, setCartItems] = useState(getDefaultCart());
    const [shippingDetails, setShippingDetails] = useState(null);

    const filterProductsByPriceRange = (minPrice, maxPrice) => {
        return productitem.filter((item) => item.price >= minPrice && item.price <= maxPrice);
      };


    const addShippingDetails = (details) => {
        setShippingDetails(details);
      };
    const findproduct=(itemName)=>{
        
        
        return new Promise((resolve) => {
            const filteredData = productitem.filter((item) => {
              return Object.values(item)
                .join("")
                .toLowerCase()
                .includes(itemName.toLowerCase());
            });
            resolve(filteredData);
          });
       
    }
    const addToCart = (itemId) => {
        console.log("itemid", itemId)
        setCartItems((prev) => ({ ...prev, [itemId]: prev[itemId] + 1 }))
    }
    const removeFromCart = (itemId) => {

        setCartItems((prev) => ({ ...prev, [itemId]: prev[itemId] - 1 }))
    }
    const getTotalCartamount = () => {
        let totalAmount = 0;
        for (const item in cartItems) {
            if (cartItems[item] > 0) {
                let itemInfo = productitem.find((product) => product.id === Number(item));
                totalAmount += cartItems[item] * itemInfo.price
            }

        }
        return totalAmount;
    }
 
    const contextValue = { cartItems, addToCart, removeFromCart, getTotalCartamount,addShippingDetails,
        shippingDetails ,findproduct,filterProductsByPriceRange}
    return <shopContext.Provider value={contextValue} >{props.children}</shopContext.Provider>
}