

import { useFormik } from 'formik';
import * as Yup from 'yup';
import TextField from './textField';
import { useState } from 'react';
import axios,{AxiosError} from 'axios';
import { useSignIn } from 'react-auth-kit';

const Login = () => {
    
   const[error, setError] = useState("");
   console.log("tokens",error)

   const signIn = useSignIn()

   const onSubmit = async(values)=>{
       console.log("Values",values);
       setError("")

       try{
        const response = await axios.post(
          "http://localhost:9000/api/v1/login"
        );

          signIn({
            token: response.data.token
          })

       }catch(err){
        if(err && err instanceof AxiosError)
        setError(err.response?.data.message);
        else if(err && err instanceof Error)
        setError(err.message);

        console.log("Error:",err)
       }
   }

    const formik = useFormik({
      initialValues: {
        email: '',
        password: '',
      },
      //validation
      validationSchema: Yup.object({
 
        password: Yup.string()
          .min(6,"(Must be minimum 6 characters)")
          .max(20, '(Must be 20 characters or less)')
          .required('(Required)'),
        email: Yup.string().email('(Invalid email)').required('(Required)'),
      }),
      onSubmit:  async values => {
        console.log("Values",values);
        setError("")
 
        try{
         const response = await axios.post(
           "http://localhost:9000/api/v1/login"
         );
        }catch(err){
         if(err && err instanceof AxiosError)
         setError(err.response?.data.message);
         else if(err && err instanceof Error)
         setError(err.message);
 
         console.log("Error:",err)
        }
        // Action to teken on form sumbit
       
      },
    });

   

    return(
      <> 
      <div className='container'>
        <div className='row login-wrap'>
          <div className='col-md-6 col-12'>
             <div className="login-bg-box"></div>
          </div>
          <div className='col-md-12 col-12'>
       
            <form onSubmit={formik.handleSubmit} className="form">
            <h1>Sign in</h1>
                  {/* {errorMessage && (
                    <p className='error'>
                      {errorMessage}
                    </p>
                  )} */}
                  <TextField type="email" name="email" display="Email Address" formik={formik} placeholder="Email address" ></TextField>
                  <TextField type="password" name="password" display="Password" formik={formik} placeholder="password" ></TextField>
                  
      
                {/* <button type="submit" className='btn btn-primary'>Submit</button> */}
                <button className="btn btn-primary btn-block with-arrow" type='submit'>Sign in</button>


                <div className="forgot-password">
                <a href="/auth/forgot">Forgot Password?</a>
                </div>
                <p>Don't have an account?<a href="/auth/signup"><b>Register Now</b></a></p>

            </form>
          </div>
        </div>
      </div>
    
      </>
    );
 
}

export default Login;