import React, { useContext } from 'react';
import { shopContext } from '../context/shopContext';

function Products(props) {


	const { id, title, images,price } = props.products;
	 const{addToCart,cartItems} = useContext(shopContext);
	 let cartitemNumber = cartItems[id]


	return (

			<div id="product-1" className="single-product" key={id}>

				<div className="part-1">
					<div className='image-container'>
						<img src={images} alt="image"></img>
					</div>
				</div>
				<div className="part-2">
					<h3 className="product-title">{title}</h3>
					<h4 className="product-price">price: <span>${price}</span></h4>
				</div>
				<button className='btn btn-primary btncart' onClick={()=>addToCart(id)} >Add to cart {cartitemNumber > 0 &&  <>({cartitemNumber})</>}</button>
				{/* <button className='viwButton'>view</button> */}
			</div>
		
		




	);
}

export default Products;