
import React   from 'react'
import {getIn} from "formik";

const TextField = (props) => {
  let hasError;
    hasError=getIn(props.formik.touched,props.name) && getIn(props.formik.errors,props.name);
  return (
     <>
        <div className={'control-group' + (hasError?' error':'') }>
            <label htmlFor={props.name}>
              {props.display}
              {hasError ? (
                  <span className='message'>{getIn(props.formik.errors,props.name)}</span>
              ) : null}
              </label>
              {props.tpe && props.type.toLowerCase()=="textarea"?(
                <textarea
                  placeholder={props.display}
                  id={props.name}
                  name={props.name}
                  {...props.formik.getFieldProps(props.name)}
                />
              ):(
                <input
                  id={props.name}
                  name={props.name}
                  type={props.type}
                  defaultChecked={props.check}
                  placeholder={props.placeholder?props.placeholder:''}
                  {...props.formik.getFieldProps(props.name)}
                />
            
            )}
       </div>
     </>
  );
};

 

export default TextField 
